import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { ToastProvider } from '../../../providers/toast/toast';
import { SpecializationsProvider } from '../../../providers/specializations/specializations';

/**
 * Generated class for the SpecializationDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-specialization-detail',
  templateUrl: 'specialization-detail.html',
})
export class SpecializationDetailPage {

  public item: any = {id: '', name: ''};

  constructor(public navCtrl: NavController, public navParams: NavParams, private toastPrv: ToastProvider,
              public viewCtrl : ViewController, private specializationPrv: SpecializationsProvider) {
    this.item = this.navParams.get('item') ?  this.navParams.get('item') : this.item;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SpecializationDetailPage');
  }

  save() {
    // TODO: validar e salvar especialidade
    if (!this.isValid()) {
      return;
    }

    let payload = this.item;

    this.specializationPrv.save(payload).subscribe((res) => {
      this.toastPrv.show('Especialização salva com sucesso!');
      this.viewCtrl.dismiss({reload: true});
    }, (err) => {
      console.log(err);
      this.toastPrv.show('Tivemos problemas em salvar o item!');
    });
  }


  isValid(): boolean {

    if (!this.item.name) {
      this.toastPrv.show('Informe o nome');
      return false;
    }

    return true;
  }

  remove() {
    this.specializationPrv.remove(this.item).subscribe((res) => {
      this.toastPrv.show('Item removido com sucesso!');
      this.viewCtrl.dismiss({reload: true});
    }, (err) => {
      console.log(err);
      this.toastPrv.show('Tivemos problemas em remover o item!');
    });
  }

  dismiss() {
    this.viewCtrl.dismiss({reload: false});
  }

}



